<template>
  <div class="verticalBar">
    <div id="myChart" ref="chart" style="width:100%;height:100%"></div>
    <div class="color_box">
      <Icon type="ios-color-palette-outline" size="22" class="iconBtn" @click="colorClick = !colorClick"/>
      <ColorPicker v-for="(color, index) in colors" :key="index" v-model="colors[index]" alpha size="small" v-show="colorClick"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'VerticalBar',
  data () {
    return {
      myChart: {},
      colors: ['#5793f3', '#d14a61'],
      colorClick: false
    }
  },
  watch: {
    colors: function (val) {
      this.myChart.setOption(this.option, true)
    }
  },
  mounted () {
    this.drawLine()
  },
  methods: {
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      this.option = {
        color: this.colors,
        title: {
          text: '标识标牌数量统计',
          subtext: '',
          textStyle: {
            fontSize: 18
          },
          left: 30,
          top: 15
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        legend: {
          data: ['红线斑块数量', '标识标牌数量'],
          top: 40,
          left: 30
        },
        grid: {
          left: '5%',
          right: '11%',
          bottom: '5%',
          top: '12%',
          containLabel: true
        },
        toolbox: {
          show: true,
          feature: {
            magicType: { show: true, type: ['line', 'bar'] },
            restore: { show: true },
            saveAsImage: { show: true }
          },
          right: 20,
          top: 40
        },
        calculable: true,
        xAxis: [
          {
            type: 'category',
            data: ['北海州', '海北州', '玉树州', '西宁市', '海东市', '黄南州', '海南州', '果洛州', '海西州']
          }
        ],
        yAxis: [
          {
            type: 'value',
            boundaryGap: [0, 0.01]
          }
        ],
        series: [
          {
            name: '红线斑块数量',
            type: 'bar',
            data: [1823, 2349, 2934, 1090, 13144, 6300, 1651, 2356, 1568]
          },
          {
            name: '标识标牌数量',
            type: 'bar',
            data: [1325, 2348, 3100, 1294, 13411, 6807, 1536, 6263, 2531]
          }
        ]
      }
      // 绘制图表
      this.myChart.setOption(this.option)
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    }
  }
}
</script>

<style lang="scss" scoped>
  .verticalBar{
    width: 100%;
    height: 100%;
    .color_box{
      position: absolute;
      right:127px;
      top:42px;
      .iconBtn {
        padding: 2px 7px 2px 7px;
        cursor: pointer;
        color: #000;
        &:hover{
          color:rgb(62, 152, 197)
        }
      }
    }
  }
</style>
