<template>
    <Modal
      title="导入采集信息"
      v-model="importState"
      class-name="vertical-center-modal"
    >
      <div class="content">
        <p>
          &nbsp;&nbsp;&nbsp;&nbsp;请上传移动端导出的标识标牌数据文件，系统会自动将其解析存储，要求文件为zip式且文件大小不能大于100M。
        </p>
        <Form ref="formValidate" :model="formData" :rules="ruleValidate" :label-width="80">
          <FormItem label="手机号码" prop="PhoneNumber">
            <Input v-model.trim="formData.PhoneNumber" placeholder="请输入采集人手机号码" size="small"></Input>
          </FormItem>
          <FormItem label="文件选择"  prop="file">
            <Upload
              v-model="formData.file"
              :before-upload="handleUpload"
              accept=".zip"
              :format="['.zip']"
              :max-size=102400
              action="#"
              ref="upload"
            >
                <Button size="small">选择文件</Button>
            </Upload>
            <span style="margin-left: 10px">
              文件名称:
              <span v-if="formData.file === null">未选择文件</span>
              <span v-if="formData.file !== null">{{ formData.file.name }}</span>
            </span>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <Button type="primary" @click="upload" :loading="loadingStatus">上传</Button>
      </div>
    </Modal>
</template>
<script>
import { importZipCsv } from '@/api/acquisitionTable'
export default {
  name: 'ATimport',
  data () {
    // 自定义验证 判断上传文件 fileList 的长度, 这样就和普通输入框表现一致了
    const validateUpload = (rule, value, callback) => {
      if (this.formData.file === null) {
        callback(new Error('请选择要上传的文件'))
      } else {
        callback()
      }
    }
    return {
      importState: false,
      file: null,
      loadingStatus: false,
      formData: {
        PhoneNumber: '',
        file: null
      },
      params: {},
      ruleValidate: {
        PhoneNumber: [
          { required: true, message: '手机号不能为空', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请填写正确的手机号码', trigger: 'blur' }
        ],
        file: [
          { required: true, validator: validateUpload, trigger: 'change' }
        ]
      }
    }
  },
  mounted () {
  },
  methods: {
    /* 上传excal坐标文件 */
    handleUpload (file) {
      this.formData.file = file
      this.$refs['formValidate'].validate(() => {})
    },
    upload () {
      let param = new FormData()
      param.append('file', this.formData.file)
      param.append('phone', this.formData.PhoneNumber)
      param.append('type', 2)
      param.append('userId', this.$store.getters.getloginInfor().userId)
      this.params = param
      this.loadingStatus = true
      this.$refs['formValidate'].validate((valid) => {
        if (valid) {
          importZipCsv(this.params).then((res) => {
            if (res.data.code === '0000') {
              this.loadingStatus = false
              if (res.data.data.isNotIn === res.data.data.num) {
                this.$Notice.error({ title: '请不要重复上传' })
              } else if (res.data.data.isNotIn === 0) {
                this.$Notice.success({ title: '上传成功' })
              } else {
                let number = res.data.data.num - res.data.data.isNotIn
                this.$Notice.error({ title: '上传总条数' + res.data.data.num + '条，' + number + '条上传成功' })
              }
              this.importState = false
              this.$emit('getNewData')
              this.init()
            } else {
              this.loadingStatus = false
              this.$Notice.error({ title: res.data.msg })
            }
          })
        } else {
          this.loadingStatus = false
        }
      })
    },
    init () {
      this.formData = {
        PhoneNumber: '',
        file: null
      }
      this.$refs.upload.clearFiles()
    }
  }
}
</script>
<style scoped lang="scss">
  .content{
    padding: 20px;
    >p{
      padding: 0 15px 25px;
      font-size: 14px
    }
  }
</style>
<style lang="scss">
  .vertical-center-modal{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: left;
    .ivu-modal{
      top: 0;
    }
  }
</style>
